@use '../base/variables' as v;
@use '../base/mixins' as m;

.about{
    @include m.tablet {
        @include m.grid(2, 3rem);
    }
    margin: 5rem auto;

    .about-image{
        display: inline-block;
    }

    .about-text{
        display: flex;
        flex-direction: column;
        justify-content: center;
        h3{
            margin: 2rem 0;
        }
        
        .about-date{
            color: v.$rosa;
            font-weight: 700;
            margin: 0;
        }
    }
}