@use '../base/variables' as v;
@use '../base/mixins' as m;

.tickets{
    background-color: v.$verde;
    padding: 5rem 0;
    h3{
        color: v.$blanco;
        text-align: center;
    }
}

.tickets-contenedor{
    @include m.grid();
    
}

.ticket{
    text-align: center;
    p{
        color: v.$blanco;
        margin: 0;
        padding: 1rem;
    }

    .ticket-header{
        background: v.$rosa;
    }

    .ticket-body{
        background: v.$blanco;

        p{
            color: v.$negro;
            text-align: left;
            border-bottom: 1px solid v.$gris;
        }

    }

    .ticket-footer{
        background: v.$morado;
        p{
            font-weight: 700;
        }
    }
}