@use '../base/variables' as v;
@use '../base/mixins' as m;

.fijar-body{
    overflow: hidden;
    background-color: #ffffff10;
}

.galeria{
    text-align: center;
    margin: 5rem 0;
}

.galeria-imagenes{
    @include m.grid(6, 2rem);
    padding: 0;
    margin: 0 5rem;

    picture:hover{
        cursor: pointer;
    }
}

.overlay {
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    place-items: center;
    position: fixed;
    width: 100%;

    picture{
        max-width: 80rem;

    }

    .div-botones{
        display: flex;
        gap: 3rem;
        @include m.tablet {
            gap: 5rem;
            
        }
    }
    
    .boton-imagenes{
        font-size: 2.5rem;
        width: 5rem;
        height: 5rem;
        text-align: center;
        align-content: center;
        margin-top: 5rem;
        font-weight: 700;
        border-radius: 50%;
        background-color: #fff;
        @include m.tablet {
            width: 7rem;
            height: 7rem
        }

        &:hover{
            cursor: pointer;
        }
    }


}