@use '../base/variables' as v;
@use '../base/mixins' as m;


.lineup{
    padding: 5rem 0 5rem 0;
    text-align: center;
    background-color: v.$rosa;

    h3, p{
        color: v.$blanco;
    }

    .lineup-days{
        margin: 5rem 0;
    }

    .escenarios-contenedor{

        @include m.tablet {
            @include m.grid();
        }
    }
}

.escenario{
    p{
        color: v.$negro;
        font-weight: 700;
    }
    ul{
        padding: 0;
        li{
            text-align: left;
            padding: 2rem;
            font-weight: 600;
            display: flex;
            .horas{
                width: 5rem;
            }
            span{
                font-weight: 400;
            }
        }

    }
}

.escenario-nombre {
    padding-top: 3rem ;
}

.escenario-azul{
    .escenario-nombre:after {
        background-image: url(../../../build/img/icono_rock.png);
        content: "";
        background-repeat: no-repeat;
        width: 20rem;
        display: block;
        background-size: cover;
        background-position: 50%;
        height: 10rem;
        margin: 2rem auto 0;
        background-size: 15rem;
    }
    background-color: v.$verde;

    li:nth-child(odd) {
        background-color: darken(v.$verde, 10);
    }
}

.escenario-amarillo{
    //Despues del nombre agrega el icono
    .escenario-nombre:after {
        //icono
        background-image: url(../../../build/img/icono_edm.png);
        //Si no se proporciona contenido, el pseudo-elemento no se representará.
        content: "";
        background-repeat: no-repeat;
        background-size: cover;
        //Posiciona al centro
        background-position: 50%;
        background-size: 15rem;
        width: 20rem;
        height: 10rem;
        display: block;
        margin: 2rem auto 0;
    }

    background-color: v.$amarillo;
    li:nth-child(odd) {
        background-color: darken(v.$amarillo, 25);
    }
}