@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
    background-color: v.$verde;
    text-align: center;

    .header-contenedor{
        display: flex;
        flex-direction: column;
        color: v.$blanco;
        align-items: center;
        padding: 2rem;
        
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            
        }

        h1{
            margin: 0;
        }

    }
}

.header-nav{
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
    gap: 2rem;
    text-align: center;
    a{
        color: v.$blanco;
        font-size: 2.4rem;
    }

    @include m.tablet {
        flex-direction: row;
        margin-top: 0rem;
    }
}
