.header {
  background-color: #4CB8B3;
  text-align: center;
}
.header .header-contenedor {
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header-contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .header-contenedor h1 {
  margin: 0;
}

.header-nav {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  gap: 2rem;
  text-align: center;
}
.header-nav a {
  color: #FFFFFF;
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .header-nav {
    flex-direction: row;
    margin-top: 0rem;
  }
}

.video {
  max-height: 70rem;
  min-height: 50rem;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.video .video-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #ff00bd;
  background: linear-gradient(270deg, rgba(255, 0, 189, 0.362), rgba(102, 0, 255, 0.479) 100%, rgba(102, 0, 255, 0.313) 0);
}
.video video {
  width: 100%;
}

.video-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
.video-text p, .video-text h2 {
  color: #FFFFFF;
  font-weight: 700;
  margin: 0;
}
.video-text h2 {
  font-size: 7rem;
}
.video-text p {
  background-color: #f53756;
  max-width: 35rem;
  font-size: 2.3rem;
  padding: 1rem 2rem;
  text-align: center;
}

.about {
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.about .about-image {
  display: inline-block;
}
.about .about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about .about-text h3 {
  margin: 2rem 0;
}
.about .about-text .about-date {
  color: #F53756;
  font-weight: 700;
  margin: 0;
}

.lineup {
  padding: 5rem 0 5rem 0;
  text-align: center;
  background-color: #F53756;
}
.lineup h3, .lineup p {
  color: #FFFFFF;
}
.lineup .lineup-days {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .lineup .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.escenario p {
  color: #000000;
  font-weight: 700;
}
.escenario ul {
  padding: 0;
}
.escenario ul li {
  text-align: left;
  padding: 2rem;
  font-weight: 600;
  display: flex;
}
.escenario ul li .horas {
  width: 5rem;
}
.escenario ul li span {
  font-weight: 400;
}

.escenario-nombre {
  padding-top: 3rem;
}

.escenario-azul {
  background-color: #4CB8B3;
}
.escenario-azul .escenario-nombre:after {
  background-image: url(../../../build/img/icono_rock.png);
  content: "";
  background-repeat: no-repeat;
  width: 20rem;
  display: block;
  background-size: cover;
  background-position: 50%;
  height: 10rem;
  margin: 2rem auto 0;
  background-size: 15rem;
}
.escenario-azul li:nth-child(odd) {
  background-color: #3b9691;
}

.escenario-amarillo {
  background-color: #FEF46D;
}
.escenario-amarillo .escenario-nombre:after {
  background-image: url(../../../build/img/icono_edm.png);
  content: "";
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  background-size: 15rem;
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0;
}
.escenario-amarillo li:nth-child(odd) {
  background-color: #eada02;
}

.fijar-body {
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.062745098);
}

.galeria {
  text-align: center;
  margin: 5rem 0;
}

.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
  padding: 0;
  margin: 0 5rem;
}
.galeria-imagenes picture:hover {
  cursor: pointer;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  place-items: center;
  position: fixed;
  width: 100%;
}
.overlay picture {
  max-width: 80rem;
}
.overlay .div-botones {
  display: flex;
  gap: 3rem;
}
@media (min-width: 768px) {
  .overlay .div-botones {
    gap: 5rem;
  }
}
.overlay .boton-imagenes {
  font-size: 2.5rem;
  width: 5rem;
  height: 5rem;
  text-align: center;
  align-content: center;
  margin-top: 5rem;
  font-weight: 700;
  border-radius: 50%;
  background-color: #fff;
}
@media (min-width: 768px) {
  .overlay .boton-imagenes {
    width: 7rem;
    height: 7rem;
  }
}
.overlay .boton-imagenes:hover {
  cursor: pointer;
}

.tickets {
  background-color: #4CB8B3;
  padding: 5rem 0;
}
.tickets h3 {
  color: #FFFFFF;
  text-align: center;
}

.tickets-contenedor {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

.ticket {
  text-align: center;
}
.ticket p {
  color: #FFFFFF;
  margin: 0;
  padding: 1rem;
}
.ticket .ticket-header {
  background: #F53756;
}
.ticket .ticket-body {
  background: #FFFFFF;
}
.ticket .ticket-body p {
  color: #000000;
  text-align: left;
  border-bottom: 1px solid #e1e1e1;
}
.ticket .ticket-footer {
  background: #752F97;
}
.ticket .ticket-footer p {
  font-weight: 700;
}

.footer {
  background-color: #F53756;
}
.footer p {
  margin: 0;
  text-align: center;
  padding: 1rem;
  color: #FFFFFF;
}