@use '../base/variables' as v;
@use '../base/mixins' as m;

.video {
    max-height: 70rem;
    min-height: 50rem;
    width: 100%;
    position: relative;
    overflow: hidden;

    .video-bg{
        width: 100%;
        height: 100%;
        position: absolute;
        background: #ff00bd;
        background: linear-gradient(270deg, rgba(255, 0, 189, .362), rgba(102, 0, 255, .479) 100%, rgba(102, 0, 255, .313) 0);
    }

    video{
        width: 100%;
    }
}

.video-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    p,h2{
        color: v.$blanco;
        font-weight: 700;
        margin: 0;
    }

    h2{
        font-size: 7rem;
    }

    p{
        background-color: #f53756;
        max-width: 35rem;
        font-size: 2.3rem;
        padding: 1rem 2rem;
        text-align: center;
    }
}


